.form {
  position: relative;
  width: 90vw;
  max-width:500px;
  padding: 10px 20px;
  background: #f4f7f8;
  margin: 10px auto;
  padding: 20px;
  background: #f4f7f8;
  border-radius: 8px;
  font-family: Georgia, "Times New Roman", Times, serif;
}
.form fieldset {
  border: none;
}
.form legend {
  font-size: 1.4em;
  margin-bottom: 10px;
}
.form input,
.form select {
  position: relative;
  font-family: Georgia, "Times New Roman", Times, serif;
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 4px;
  font-size: 15px;
  margin: 0;
  outline: 0;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  background-color: #e8eeef;
  color: #8a97a0;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  margin-bottom: 30px;
}
.form input:focus,
.form select:focus {
  background: #d2d9dd;
}
.form select {
  -webkit-appearance: menulist-button;
}
.form select {
  overflow: scroll;
}

.form .number {
  background: #1abc9c;
  color: #fff;
  height: 30px;
  width: 30px;
  display: inline-block;
  font-size: 0.8em;
  margin-right: 4px;
  line-height: 30px;
  text-align: center;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
  border-radius: 15px 15px 15px 0px;
}

.form input[type="submit"],
.form input[type="button"] {
  position: relative;
  display: block;
  padding: 19px 39px 18px 39px;
  color: #fff;
  margin: 0 auto;
  background: #1abc9c;
  font-size: 18px;
  text-align: center;
  font-style: normal;
  width: 100%;
  border: 1px solid #16a085;
  border-width: 1px 1px 3px;
  margin-bottom: 10px;
}
.form .radio {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  height: 40px;
  color: #8a97a0;
  padding: 0;
  width: 100%;
  position: relative;
}
.radio input[type="radio"] {
  width: 20px;
  height: 20px;
  margin-right: 2vw;
  margin-left: 2vw;
}
.form input:disabled{
  background-color: rgb(255, 104, 104);
}
.form input[type="submit"]:disabled:hover{
  background-color: rgb(248, 82, 82);
}
.form input[type="submit"]:hover,
.form input[type="button"]:hover {
  background: #109177;
}
